.container{
   
    text-align: center;

}
.title{
    margin-top: 50px;
    color: white;
    font-size: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    

}
.title span{
    color: #26ffcb;
    padding-left: 15px;

}
#divclass{
    display: flex;
    margin: auto;
    justify-content: center;
    text-align: center;
}
.buttonclass{
    display: flex;
    margin: auto;
    justify-content: center;
    text-align: center;
}
.reset{
    
   
    width: 150px;
  
    height: 50px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 50px;
    background: #1f3540;
    font-size: 26px;
    color: #26ffcb;
    margin-top: 10px;
    margin-bottom: 50px;
    
    
   

}

.boxes{
    display: flex;
    height: 150px;
    width: 160px;
    background: #1f3540;
    border: 4px solid #0f1b21;
    border-radius: 12px;
    cursor: pointer;

}
.board{
    height: 500px;
    width: 500px;
    display: flex;
    margin: auto;

}

.boxes img{
    margin: 50px 50px;
}

.title img{
    padding: 20px 20px;
    height: 50px;
}

